import React, { useState, useEffect } from 'react'
import './Post.css'
import Avatar from '@material-ui/core/Avatar'
import {Button} from '@material-ui/core'
import { db } from './firebase';
import firebase from 'firebase'

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

 

function Post({name,postId,user,Postusername,imageUrl,caption}) {
    const [comments, setComments] = useState([]);
    const [comment, setComment] = useState("");

    useEffect(()=>{
        let unsubscribe;
        if (postId){
            unsubscribe = db
                            .collection("posts")
                            .doc(postId)
                            .collection('comments')
                            .orderBy('timestamp','desc')
                            .onSnapshot(snapshot=>{
                                setComments(snapshot.docs.map((doc)=> doc.data()));
                            })
        }

        return ()=>{
            unsubscribe();
        }
    }, [postId])

    const postComment=(event)=>{
            event.preventDefault();
            db.collection('posts').doc(postId).collection('comments').add({
                text:comment,
                username: user.displayName,
                timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            });
            setComment('')
    }
    //----------


    const [open, setOpen] = React.useState(false);
  
    const handleClick = () => {
      setOpen(true);
    };
  
    const handleClose = (event, reason) => {
      if (reason === 'clickaway') {
        return;
      }
  
      setOpen(false);
    };
    //--------------

    return (
        <div className='post'>

        {/*  */}
        {/* <Button onClick={handleClick}>Open simple snackbar</Button> */}
    <Snackbar
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={open}
      autoHideDuration={2000}
      onClose={handleClose}
      message="Note archived"
      action={
        <React.Fragment>
      
          <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
            <CloseIcon fontSize="small" />
          </IconButton>
        </React.Fragment>
      }
    />
        {/*  */}

     
            <div className='post_header'>
            <div className='post_header_1'>
            <Avatar 
                className='post_avatar'
                alt={Postusername}
                style={{textTransform:'capitalize'}}
                src='/imgages'
            />
            <h3 style={{textTransform:'capitalize'}}>{Postusername}</h3>
            </div>
                    {(user && Postusername==user.displayName)?(<Button 
                    onClick={
                      (event)=>{ db.collection('posts').doc(postId).delete()}
                    }
                    variant="contained" color="secondary" disableElevation>
                       Delete
                     </Button>):(
                         <p></p>
                     )}
                   
            </div>
            <img 
                className='post_img'
                src= {imageUrl}
                alt='posted'
            />
            <h4 className='post_text'><strong>{Postusername}</strong> {caption}</h4>

            <div className='post_comments'>
                {
                    comments.map((comment)=>(
                        <p>
                            <strong>{comment.username}</strong> {comment.text}
                        </p>  
                    ))
                }
            </div>

                {user &&(
                    <form className='post_commentBox'>
                <input
                className='post_input'
                type='text'
                placeholder='Add a Comment...'
                value={comment}
                onChange={(e)=> setComment(e.target.value)}
                 />
                 <button
                 type='submit'
                 disabled={!comment}
                 onClick={postComment}
                 className='post_button'   
                  > 
                  Post
                  </button>
            </form>
                )
                }

                <div>
     
    </div>
        </div>
    )
}

export default Post;

