import React, {useState, useEffect} from 'react';
import './App.css';
import Post from './Post';
import { db, auth } from './firebase';
import Modal from '@material-ui/core/Modal'
import {makeStyles} from '@material-ui/core/styles'
import { Button, Input, Avatar } from '@material-ui/core';
import LinearProgress from '@material-ui/core/LinearProgress';
import ImageUpload from './ImageUpload';


function getModalStyle() {
  const top = 50 ;
  const left = 50 ;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));




function App() {
  const classes = useStyles();
  const [modalStyle] = useState(getModalStyle);

  const [posts, setPosts] = useState([]);
  const [open, setOpen] = useState(false);
  const [openSignIn, setOpenSignIn] = useState(false);
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [user, setUser] = useState(null);
  const [name, setName] = useState(null)
  const [dis,setDis] = useState('0');
  const [hid, setHid] = useState('')

  useEffect(()=>{
    const unsubscribe= auth.onAuthStateChanged((authUser) =>{
      if(authUser){
          setUser(authUser)
      }else{
          setUser(null)
      }
    })

    return () =>{
      unsubscribe();
    }
  },[username, user, name]);

useEffect(() => {
  db.collection('posts').orderBy('timestamp','desc').onSnapshot(snapshot=>{
    setPosts(snapshot.docs.map(doc=> ({
       id:doc.id,
       post:doc.data() })))
  })
}, [])

  const signUp=(event)=>{
      event.preventDefault();
      auth.createUserWithEmailAndPassword(email, password)
          .then((authUser)=>{
            return authUser.user.updateProfile({
              displayName:username,
              user: authUser
            })
          })
          .catch((error) => {
            alert(error.message);
              setOpen(true);
              setHid('')  
          })
     setName(username);
     setUsername(username);   
     setOpen(false);
     setHid('hidder')
    
  }

  const signIn =(event)=>{
    event.preventDefault();

    auth.signInWithEmailAndPassword(email, password)
        .catch((error) =>{
          alert(error.message);
            setOpenSignIn(true)
            setHid('')  
        })
        setOpenSignIn(false);
        setHid('hidder')
  }
  return (
    <div className="App">
    <Modal
      open={open}
      onClose={()=> setOpen(false)}
    >
    <div style={modalStyle} className={classes.paper}>
     <center>
    
    <img 
      src='https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png'
      alt='Instagram'
    />
    </center>
     <form className='app_signUp'>
    <Input 
      type='text'
      value={username}
      onChange={(e) => setUsername(e.target.value)}
       placeholder="Username" />
      <Input 
      type='text'
      value={email}
      onChange={(e) => setEmail(e.target.value)}
       placeholder="Email" />
      <Input 
      type='password'
      value={password}
      onChange={(e) => setPassword(e.target.value)}
      placeholder="Password" />
     
     <Button type='submit' onClick={signUp}>Sign Up</Button>
     </form>
      </div>
    </Modal>


    <Modal
      open={openSignIn}
      onClose={()=> setOpenSignIn(false)}
    >
    <div style={modalStyle} className={classes.paper}>
     <center>
    
    <img 
      src='https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png'
      alt='Instagram'
    />
    </center>
     <form className='app_signUp'>
      <Input 
      type='text'
      value={email}
      onChange={(e) => setEmail(e.target.value)}
       placeholder="Email" />
      <Input 
      type='password'
      value={password}
      onChange={(e) => setPassword(e.target.value)}
      placeholder="Password" />
     
     <Button type='submit' onClick={signIn}>Sign In</Button>
     </form>
      </div>
    </Modal>

   
    <div id='app_header'>
  
    <img 
      className='header_image'
      src='https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png'
      alt='Instagram'
    />
 
   

{
 
      user? (
        <div className='app_right'>
        <Button variant='outlined' onClick={() => auth.signOut()}>{user?.displayName?(user.displayName):(name)} Ë… </Button>
        <Avatar 
                className='post_avatar'
                alt={user?.displayName?(user.displayName):(name)}
                style={{textTransform:'capitalize'}}
                src='/imgages'
            />
            </div>
      ) : (
        <div className='app_loginContainer'> 
        <Button onClick={() => {setOpen(true);setHid('')}}>Sign Up</Button>
        <Button onClick={() => {setOpenSignIn(true);setHid('')}}>Sign In</Button>
        </div>
      )
    }

    </div>
    <LinearProgress className= {hid} />
 
 
    <div className='app_posts'>

    {
      user?(
        <ImageUpload uploadname={name?(name):(user.displayName)} />
      ):(
        <p></p>
      )
    }
    
    {
      posts.map(({post,id})=>(
        <Post key={id} postId={id} name={name} user={user} Postusername={post.username} imageUrl={post.imageUrl} caption={post.caption} />
      ))
    }
    </div>
   


    {
      user? (
        <h3 style={{textAlign:'center', marginTop:'10px', border:'1px solid lightgray', padding:'10px'}}>Wanna post your story tooo... <a href='#cont'><Button variant='outlined'>Post</Button></a>
        </h3>
      ):(
          <h3 style={{textAlign:'center', marginTop:'10px', border:'1px solid lightgray', padding:'10px'}}>Sorry you Need to Login to Upload</h3>
      )
    }
    </div>

  );
}

export default App;